import React, { useState, useEffect } from 'react';
import BookmarkRepos  from '../components/BookmarkRepos';

const RepoEight = () => {

    const [repoInfo, setRepoInfo] = useState({ bookmarks: 0 });
    const name = 'awesome';

    useEffect(() => {
        const fecthData = async() =>{
          const result = await fetch(`/api/repos/${name}`);
          const body = await result.json();
          console.log(body);
          setRepoInfo(body);
        }
        fecthData();
    }, [name]);


    return (
        <>
        <BookmarkRepos repoName={name} bookmarks={repoInfo.bookmarks} setRepoInfo={setRepoInfo}/>
        </>
    );
}

export default RepoEight;