import React from 'react';

const BookmarkRepos = ({repoName, bookmarks, setRepoInfo }) => {
    const bookmarkRepo = async () => {
        const result = await fetch(`/api/repos/${repoName}/bookmark`, {
            method: 'post',
        });
        const body = await result.json();
        setRepoInfo(body);
    }
    return (
        <div id="">
            <button className="btn btn-primary" onClick={() => bookmarkRepo()}>Bookmark this ( {bookmarks} ) </button>
        </div>
    );
};

export default BookmarkRepos;
